.formControlInput, 
.formControlTextarea {
    border-radius: 15px;
    padding-inline: 10px;
    border: 1px solid rgba(0, 0, 0, 0.3);
    margin-block: 15px;

     /* Шрифт */
     font-size: 16px;
     font-family: 'Roboto', sans-serif;
     color: #333;
     
     /* Рамка и скругление углов */
     border: 2px solid #7e7b7b;
     border-radius: 30px;
     
     /* Тени и фон */
     background-color: #EAE7E2;
     box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.formCheckbox {
    border-radius: 15px;
    margin-left: 10px;
}

.formControlInput {
    height: 30px;
    max-width: 180px;
    min-width: 180px;
    width: 100%;
    max-width: 225px;
    padding: 10px 20px;
    /* Переходы для плавных эффектов */
    transition: all 0.3s ease;
}

.formControlTextarea {
    height: 40px;
    padding: 0 20px;
    line-height: 35px;
    overflow: hidden;
    resize: none;
}

.formControlInput.error,
.formControl.error
{   
    border: 2px solid rgb(158, 4, 4);
}

.error {
    display: flex;
    color: rgb(158, 4, 4);;
}

.formSummaryError {
    border: red 1px solid;
    padding: 5px;
    color: rosybrown;
}

.createField {
    width: 100%;
    max-width: 225px;
    padding: 10px 20px;
    
    /* Шрифт */
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    color: #333;
    
    /* Рамка и скругление углов */
    border: 2px solid #7e7b7b;
    border-radius: 30px;
    
    /* Тени и фон */
    background-color: #EAE7E2;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    
    /* Переходы для плавных эффектов */
    transition: all 0.3s ease;
    textarea {
        background-color: white;
        max-width: 180px;
        min-width: 180px;   
    }
}
